.aboutMe-container {
  width: 127.5%;
  position: relative;
  left: -15%;
  height: 383px;
  background-color: #eaf2fd;

  .aboutMe-text {
    padding: 32px 152px 32px 12%;

    p {
      width: 751px;
    }
  }
}

@media only screen and (max-width: 1024px) {
  .aboutMe-container {
    width: 100%;
    left: 0;
    height: 290px;

    .aboutMe-text {
      padding: 32px;

      p {
        width: 90%;
      }
    }
  }
}

@media only screen and (max-width: 600px) {
  .aboutMe-container {
    height: 300px;
    .aboutMe-text {
      padding: 10px;
    }
  }
}
